@import 'vars';

.header{
    height: 45px;
    padding: 5px 5px;
    background: linear-gradient(var(--green-light), var(--green-dark));
    display: flex;
    align-items: center; 
    justify-content: space-between; 
}
.header__div-iconMenu{
    width: 30px;
}

.svg-inline--fa{ /*Cambio color icono SVG*/
    color: var(--white);
}

.header_search{
    width: 70%;
    height: 30px;
    border-radius: 10px;
    background: var(--white);
    display: flex;
    align-items: center; 
    padding-left: 10px;
    padding-right: 10px;
}

.header__div-iconWalt{
    width: 5%;
}

.header__div-iconWalt img{
    width: 30px;
}

.header_search-input{
    width: 90%;
    color: gray; 
    border: none;
    outline: none;
}

.header_search-icon{
    width: 20px;
}

@media (max-width: 320px) {
    .header{
        width: 320px;
    }
}