@import 'vars';

.characterHome{
    width: 250px;
    height: 200px;
    border-radius: 10px;
    border: 3px solid gray;
    padding: 5px 5px;
    background: linear-gradient(var(--yellow-light), var(--yellow-bar));
    display: flex;
    flex-direction: column;
    
}
.characterHome__name{
    font-size: var(--lg);
    font-family: 'Pushster', cursive;
    font-weight: normal;
    color: green;
    text-align: center;
    margin-bottom: 5px;
}

.characterHome__div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 180px;
}

.characterHome__image{
    margin-right: 5px;
}
.characterHome__image img{
    width: 120px;
    border-radius: 5px;
}
.characterHome__description{
    height: 240px;
    width: 40%;
    display: flex;
    flex-direction: column; 
    align-items: left; 
    justify-content: center; 
}



.characterHome__description-item{
    margin-top: 5px;
    font-size: var(--xsm);
    font-family: 'Roboto Mono', monospace;
    font-weight: bold;
    color: #006400;
}

.characterHome__description-element{
    margin-top: 5px;
    font-size: var(--xsm);
    font-family: 'Roboto Mono', monospace;
    font-weight: normal;
    text-align: right;
}

.characterHome__description-occupation{
    margin-top: 5px;
    font-size: 10px;
    font-family: 'Roboto Mono', monospace;
    font-weight: normal;
    text-align: left;
}

@media (max-width: 640px) {
	.cards-container {
		grid-template-columns: repeat(auto-fill, 240px);
    }     
}