@import 'vars'; 

.characterInfo{
    margin: 10px 10px;
    height: 500px;
    display: flex; 
    align-items: center;
    flex-direction: column;
    border: 1px solid gray;
}

.characterInfo__title{
    font-size: var(--sm);
    color: gray;
    font-family: 'Roboto Mono', monospace;
}

.characterInfo__property{
    width: 100%;
    margin: 2px 2px;
    height: 350px;
    display: flex;
    align-items: center;
    background: blue;
}

.characterInfo__property-data{
    margin: 2px 2px;
    width: 50%;
    height: 340px;
    background: green;
}

.characterInfo__property-img{

    margin: 2px 2px;
    height: 340px;
    width: 50%;
}

.characterInfo__property-img img{
    width: 100%;
    border-radius: 5px;
}



.characterInfo__annexed{
    width: 98%;
    margin: 2px 2px;
    height: 150px;
    background: pink;
}